/* Styles for the MarketData component
 *
 * This stylesheet contains only the CSS rules required by the
 * ``MarketData.js`` React component.  Unused selectors from the
 * original file have been removed, and the remaining rules mirror
 * the structure of the JSX markup: a container, a search row with
 * filter groups and inputs, a suggestions list and a results grid.
 */

/* Outer container for the market data page */
.market-data {
  width: 80%;
  margin: 0 auto;
  padding: 10px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3), 0 0 0 1px rgba(255, 255, 255, 0.05);
}

/* Generic label styling for form inputs */
.input-label {
  margin-bottom: 2px;
  font-size: 0.85rem;
  color: #bfc7d5;
  font-weight: 500;
  letter-spacing: 0.01em;
}

/* Search row holds the search bar, filters and the search button */
.search-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 32px;
  width: 100%;
  min-height: 44px;
  margin-bottom: 20px;
}

/* Adjust layout on narrow screens */
@media (max-width: 900px) {
  .search-row {
    flex-direction: column;
    align-items: stretch;
    gap: 12px;
  }
}

/* Group for the search bar and suggestions */
.search-bar-group {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  min-width: 220px;
  max-width: 400px;
  flex: 2 1 340px;
  margin-right: 0;
  position: relative;
}

/* Label above the search input */
.search-bar-group .input-label {
  margin-bottom: 2px;
  margin-left: 2px;
  font-size: 0.85rem;
  color: #bfc7d5;
  font-weight: 500;
  letter-spacing: 0.01em;
}

/* Make the input span the full width of its container */
.search-bar-group .search-input {
  width: 100%;
}

/* Suggestion list shown beneath the search input */
.search-bar-group .suggestions {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 2000;
  background: rgba(40, 60, 90, 0.98);
  border: 1px solid rgba(102, 126, 234, 0.18);
  border-top: none;
  border-radius: 0 0 8px 8px;
  max-height: 280px;
  overflow-y: auto;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.18);
  scrollbar-width: none; /* Firefox */
}

/* Hide scrollbars in WebKit browsers */
.search-bar-group .suggestions::-webkit-scrollbar {
  display: none;
}

/* Individual suggestion item */
.suggestion-item {
  padding: 12px 16px;
  color: #ffffff;
  cursor: pointer;
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
  transition: all 0.2s ease;
  font-size: 0.95rem;
  font-weight: 500;
  background: transparent;
}

/* Highlight the active suggestion or hover state */
.suggestion-item.active,
.suggestion-item:hover {
  background: rgba(102, 126, 234, 0.15);
  color: #ffffff;
}

/* Remove the bottom border on the last suggestion */
.suggestion-item:last-child {
  border-bottom: none;
}

/* Container for each filter (Tier/Enchantment) */
.filter-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 120px;
  height: 44px;
  justify-content: flex-start;
  gap: 0;
}

/* Base styles for select elements and the search button */
.filter-dropdown {
  min-width: 120px;
  height: 44px;
  padding: 8px 12px;
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 8px;
  color: #ffffff;
  font-size: 0.9rem;
  font-weight: 500;
  outline: none;
  transition: all 0.3s ease;
  cursor: pointer;
}

/* Option styling inside select */
.filter-dropdown option {
  background: #2d3748;
  color: #ffffff;
  padding: 8px;
}

/* Focus state for dropdowns */
.filter-dropdown:focus {
  border-color: rgba(102, 126, 234, 0.6);
  box-shadow: 0 0 0 2px rgba(102, 126, 234, 0.1);
  background: rgba(255, 255, 255, 0.12);
}

/* Search button inherits dropdown base but overrides colours */
.search-btn {
  font-size: 1rem !important;
  font-weight: 600;
  width: 5%;
  background: linear-gradient(90deg, #667eea 0%, #764ba2 100%) !important;
  color: #fff;
  border: none;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.3s;
}

.search-btn:hover {
  background: linear-gradient(90deg, #5a67d8 0%, #6b46c1 100%);
}

/* Empty class reserved for optional alignment tweaks */
.search-btn-right {
  /* No specific styles, but the class can be used for alignment if needed */
}

/* Search input element */
.search-input {
  min-width: 340px;
  width: 100%;
  max-width: 100%;
  font-size: 1rem;
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 8px;
  color: #ffffff;
  outline: none;
  transition: all 0.3s ease;
  font-weight: 500;
  padding: 8px 12px;
  box-sizing: border-box;
}

.search-input:focus {
  border-color: rgba(102, 126, 234, 0.6);
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1), 0 8px 24px rgba(102, 126, 234, 0.15);
  background: rgba(255, 255, 255, 0.12);
}

.search-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
  font-weight: 400;
}

/* Grid for displaying the MarketItemCard components */
.results-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.2rem;
  justify-items: center;
  align-items: start;
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
}

/* When the screen is narrow, stack cards in a single column */
@media (max-width: 900px) {
  .search-row .search-input,
  .search-row .filter-group,
  .search-row .search-btn {
    min-width: 0;
    max-width: 100%;
    width: 100%;
  }
  .results-section {
    grid-template-columns: 1fr;
  }
}