/* Styles for the PriceChart component
 *
 * This stylesheet has been trimmed down to include only the
 * declarations needed by ``PriceChart.js``.  Unused selectors such
 * as city checkboxes, item headers and image containers have been
 * removed.  The remaining classes map directly to the elements
 * rendered by the component: a container row, the chart container
 * itself, optional modifiers (``full-width`` and ``minimal-padding``)
 * and the canvas element.
 */

/* The outer row wrapper for the chart */
.price-chart-container-row {
  display: flex;
  flex-direction: column;
  width: 100%;
}

/* Remove padding and margin when ``minimal-padding`` is applied */
.price-chart-container-row.minimal-padding {
  padding: 0 !important;
  margin: 0 !important;
}

/* Base container for the chart; provides background and spacing */
.price-chart-container {
  flex: 1 1 0;
  min-width: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background: rgba(40, 60, 90, 0.18);
  border-radius: 8px;
  padding: 10px;
  margin-bottom: 12px;
  box-sizing: border-box;
}

/* When ``full-width`` is added the chart container stretches across
 * its parent, centres the chart and removes its own padding/margin.
 */
.price-chart-container.full-width,
.price-chart-container.full-width.minimal-padding {
  width: 100% !important;
  flex: 1 1 auto;
  min-width: 0;
  min-height: 140px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 !important;
  margin: 0 !important;
}

/* The canvas element rendered by Chart.js */
.price-chart-canvas {
  background: transparent;
  display: block;
}